import { HmacSHA256 } from 'crypto-js';

export const __ = (str) => {
    return str;
}
export const numberFormat = (number, decPlaces = 0, decSep = '.', thouSep = ',') => {
    return number.toFixed(decPlaces).toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
}

export const getAuth = () =>{
    const salt = "20210531";
    let token = window._a
    let secret = window._b+salt;
    let auth = token.split(".")
    let header = {
        alg: "HS256",
        typ: "jwt"
    }
    let payload = JSON.parse(atob(auth[1]))

    let HP = btoa(JSON.stringify(header))+"."+btoa(JSON.stringify(payload));
    let hash = HmacSHA256(HP, secret)
    let signature = btoa(hash.toString());
    let _token = HP+"."+signature
    console.clear()
    console.log(_token, token)
    // console.log(header, payload, secret)
    // console.log(HP);
    // console.log(hash.toString())

    if( _token == token) return payload
    else return false
}