import Sidebar from "../../components/sidebar"
import JSONTree from 'react-json-tree'
import * as helper from './../../utils/helper';
import _ from 'lodash';
import {
    Link
} from 'react-router-dom'
import React, { useRef, useLayoutEffect } from 'react';
import axios from 'axios';
import { useHistory } from "react-router-dom";
const theme = {
    scheme: 'monokai',
    author: 'wimer hazenberg (http://www.monokai.nl)',
    base00: '#272822',
    base01: '#383830',
    base02: '#49483e',
    base03: '#75715e',
    base04: '#a59f85',
    base05: '#f8f8f2',
    base06: '#f5f4f1',
    base07: '#f9f8f5',
    base08: '#f92672',
    base09: '#fd971f',
    base0A: '#f4bf75',
    base0B: '#a6e22e',
    base0C: '#a1efe4',
    base0D: '#66d9ef',
    base0E: '#ae81ff',
    base0F: '#cc6633',
};

axios.defaults.headers.post['Content-Type'] ='application/x-www-form-urlencoded';


class Request extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            request: {
                id: 0,
                name: "",
                description: "",
                parent:0,
                sort:0,
                type:"REQUEST",
                request_type:"GET",
                uri: "",
                request_params:[],
                request_header:[],
                request_body:[]
            },
            response:{
                data: {},
                status: 200,
                statusText: "OK"
            },
            editable: {},
            opt: {
                request_type: ["GET", "POST", "PUT", "DELETE"],
                headers: [
                    {key: "Authorization", val: false},
                    {key: "Accept", val: ["application/json"]},
                    {key: "Accept-Encoding", val: []}
                ],
                field_type: ["string", "binary", "int", "float"]
            },
        }

        this.sidebar = React.createRef();
    }

    _getRequest = () => {
        console.log(this.props)
        axios.get(window._baseUrl+"/apiman/requests?id="+this.props.match.params.id).then(res=>{
            if(res.data){
                this.setState({
                    request: {
                        ...res.data,
                        request_header: JSON.parse(res.data.request_header),
                        request_params: JSON.parse(res.data.request_params),
                        request_body: JSON.parse(res.data.request_body)
                    }
                })
            }
        })
    }

    _handleChangeValue = (key, e, editable = false) => {
        let {request} = this.state

        if(e.target.files && e.target.files.length > 0) {
            _.set(request, key, e.target.files[0])
        }else{
            _.set(request, key, e.target.value)
        }

        if(editable){
            this.setState({
                request: request,
                editable: {
                    ...this.state.editable,
                    [key]: !this.state.editable[key]
                }
            })
        }else{
            this.setState({
                request: request
            })
        }

    }

    _handleEditInput = (id) =>{
        if(helper.getAuth() && helper.getAuth().role == "editor"){
        this.setState({
                editable: {
                    ...this.state.editable,
                    [id]: !this.state.editable[id]
                }
            }
        )
        }
    }

    _handleTestRequest = () => {
        const {request} = this.state;
        let _headers = {};
        let _params = [];
        let params = "";
        if(request.request_params.length>0){
            _params = request.request_params.map(item=>{
                return item.key + "=" + item.value
            })
            params = "?"+_params.join('&')
        }

        if(request.request_header.length>0){
            request.request_header.forEach(item=>{
                _headers[item.key] = item.value
            })
        }

        let url = request.uri ? window._baseUrl + request.uri + params : window._baseUrl + "/" + params
        if(["POST", "PUT", "DELETE"].indexOf(request.request_type) >= 0){
            let formData = new FormData();
            request.request_body.forEach((item)=>{
                formData.append(item.key, item.value)
            })
            axios.post(url, formData, {
                headers: _headers
            }).then(res=>{
                let response;
                this.setState({
                    response: res
                })
            }).catch(e=>{
                if(!e.response){
                    this.setState({
                        response: {
                            data: {"msg": "NetworkError"},
                            status: "N/A",
                            statusText: "NetworkError"
                        }
                    })
                }else{
                    this.setState({
                        response: {...e.response, data: {"response": e.response.data}}
                    })
                }

            })
        }else{
            axios.get(url, {
                headers: _headers
            }).then(res=>{
                this.setState({
                    response: res
                })
            }).catch(e=>{
                if(!e.response){
                    this.setState({
                        response: {
                            data: {"msg": "NetworkError"},
                            status: "N/A",
                            statusText: "NetworkError"
                        }
                    })
                }else{
                    this.setState({
                        response: {...e.response, data: {"response": e.response.data}}
                    })
                }

            })
        }
    }

    _handleAddRequest = (type) => {
        if(type == 'header'){
            let request_header = this.state.request.request_header;
            request_header.push({key: "xxx", value: "", desc: ""});
            this.setState({
                request: {
                    ...this.state.request,
                    request_header: request_header
                }
            })
        }
        if(type == 'params'){
            let request_params = this.state.request.request_params;
            request_params.push({key: "", value: "", desc: ""});
            this.setState({
                request: {
                    ...this.state.request,
                    request_params: request_params
                }
            })
        }

        if(type == 'body'){
            let request_body = this.state.request.request_body;
            request_body.push({key: "", type: "string", value: "", desc: ""});
            this.setState({
                request: {
                    ...this.state.request,
                    request_body: request_body
                }
            })
        }

    }

    _handleUpdateRequest = () => {
        console.log(this.props)
        let data = this.state.request
        data._method = "PUT"
        axios.post(window._baseUrl+'/apiman/requests', data).then(res=>{
            console.log(res)
            this._getRequest()
            this.sidebar.current.handleGetRequests();
        })
    }

    componentDidMount() {
        console.log("DIDMOUNT")
        this._getRequest()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        console.log("DIDUPDATE", this.props.match.params, prevProps.match.params)
        if(this.props.match.params != prevProps.match.params)
            this._getRequest()
    }

    render() {
        const {request, opt, response, editable} = this.state

        return (
            <>
                <Sidebar  ref={this.sidebar}/>
                <div className="main-db-right">
                    <div className="main-right-content">
                        <div className="main-title-create mt-5">
                            {editable.name ? (
                                <input className={"form-control"} value={request.name}
                                       onBlur={(e)=>this._handleChangeValue("name", e, true)}
                                       onChange={(e)=>this._handleChangeValue("name", e)}/>
                            ) : (
                                <h5 onDoubleClick={()=>this._handleEditInput("name")} className="font-weight-bold content-title-create event-click-input">{request.name}</h5>
                            )}

                        </div>
                        <div className="main-content-db main-create-device">

                            <div className="main-form-device-create">
                                <div className="main-select-form">
                                    <div className="main-cus-select">
                                        <select className={"form-select w-auto float-right bg-method cus-select "+request.request_type}
                                                aria-label="Default select example"
                                                value={request.request_type}
                                                onChange={(e)=>this._handleChangeValue("request_type", e)}
                                                readOnly={true}
                                        >
                                            {(helper.getAuth() && helper.getAuth().role == "editor") ? (
                                                opt.request_type.map((item, key)=> {
                                                        return (
                                                            <option key={key} value={item}>{item}</option>
                                                        )
                                                    }
                                                )
                                            ):(
                                                <option value={request.request_type}>{request.request_type}</option>
                                            )}
                                        </select>
                                        <svg width="7" height="6" viewBox="0 0 7 6" fill="none"
                                             xmlns="http://www.w3.org/2000/svg">
                                            <path d="M3.5 6L0.468912 0.749999L6.53109 0.75L3.5 6Z" fill="#C4C4C4"/>
                                        </svg>
                                    </div>
                                    <div className="main-cus-select">
                                        <select className="form-select w-auto float-right select-domain-cus pr-0"
                                                aria-label="Default select example"
                                        defaultValue={window._baseUrl}>
                                            <option>{window._baseUrl}</option>
                                        </select>
                                        <svg width="7" height="6" viewBox="0 0 7 6" fill="none"
                                             xmlns="http://www.w3.org/2000/svg">
                                            <path d="M3.5 6L0.468912 0.749999L6.53109 0.75L3.5 6Z" fill="#C4C4C4"/>
                                        </svg>
                                    </div>
                                    <input
                                        type="text"
                                        className="form-control pl-0"
                                        value={request.uri}
                                        onChange={(e)=>this._handleChangeValue("uri", e)}
                                        readOnly={(!helper.getAuth() || helper.getAuth().role != "editor")}
                                    />
                                    <button className="btn btn-secondary bg-blue btn-cus-submit" onClick={()=>this._handleTestRequest()}>
                                        <label>Submit</label>
                                    </button>
                                    {(helper.getAuth() && helper.getAuth().role == "editor") ? (
                                        <>
                                            <button className="btn btn-secondary bg-blue btn-cus-submit-save item-btn-submit"
                                                    onClick={this._handleUpdateRequest}>
                                                <i className="fa fa-save"></i>
                                            </button>
                                            <button className="btn btn-secondary bg-blue btn-cus-submit-delete item-btn-submit">
                                                <i className="fas fa-trash"></i>
                                            </button>
                                        </>
                                    ):(<></>)}
                                </div>
                            </div>
                                {editable.description ? (
                                    <div className="main-note-form-create event-click-area">
                                            <textarea className={"form-control"} value={request.description}
                                           onBlur={(e)=>this._handleChangeValue("description", e, true)}
                                           onChange={(e)=>this._handleChangeValue("description", e)}/>
                                    </div>
                                ) : (
                                    <div  onDoubleClick={()=>this._handleEditInput("description")} className="main-note-form-create event-click-area">
                                        <p>{request.description}</p>
                                    </div>
                                )}
                            <div className="main-form-type">
                                <div className="row">
                                    <div className="col-md-7">
                                        <div className="card border-none">
                                            <div className="card-body">
                                                <h5 className="card-title font-weight-bold">
                                                    Request Headers
                                                    <span
                                                        onClick={() => this._handleAddRequest("header")}
                                                        style={{cursor: "pointer"}}>
                                                        <i className={"fas fa fa-plus-square"}></i>
                                                    </span></h5>
                                                <table className="table w-100 table-bordered">
                                                    <thead>
                                                        <tr>
                                                            <th scope="col">Field name</th>
                                                            <th scope="col">Value</th>
                                                            <th scope="col">Description</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                    {
                                                        request.request_header.length>0 ? request.request_header.map((item, k)=>{
                                                            return (
                                                                <tr key={k}>
                                                                    <th scope="row">
                                                                        <input type="text" value={item.key}
                                                                               className="form-control"
                                                                            onChange={(e)=>this._handleChangeValue("request_header["+k+"].key", e)}
                                                                        />
                                                                    </th>
                                                                    <td>
                                                                        <input
                                                                            value={item.value}
                                                                            onChange={(e)=>this._handleChangeValue("request_header["+k+"].value", e)}
                                                                            type="text" className="form-control"/>
                                                                    </td>
                                                                    <td>
                                                                        <input
                                                                            value={item.desc}
                                                                            onChange={(e)=>this._handleChangeValue("request_header["+k+"].desc", e)}
                                                                            type="text" className="form-control"/>
                                                                    </td>
                                                                </tr>
                                                            )
                                                        }) : (<></>)
                                                    }

                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>

                                        <div className="card border-none">
                                            <div className="card-body">
                                                <h5 className="card-title font-weight-bold">Request Params
                                                    <span
                                                        onClick={() => this._handleAddRequest("params")}
                                                        style={{cursor: "pointer"}}>
                                                        <i className={"fas fa fa-plus-square"}></i>
                                                    </span>
                                                </h5>
                                                <table className="table w-100 table-bordered">
                                                    <thead>
                                                        <tr>
                                                            <th scope="col">Field name</th>
                                                            <th scope="col">Value</th>
                                                            <th scope="col">Description</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                    {
                                                        request.request_params.length>0 ? request.request_params.map((item, k)=>{
                                                            return (
                                                                <tr key={k}>
                                                                    <th scope="row">
                                                                        <input type="text"
                                                                               value={item.key}
                                                                               onChange={(e)=>this._handleChangeValue("request_params["+k+"].key", e)}
                                                                               className="form-control"/>
                                                                    </th>
                                                                    <td>
                                                                        <input value={item.value}
                                                                               onChange={(e)=>this._handleChangeValue("request_params["+k+"].value", e)}
                                                                               type="text" className="form-control"/>
                                                                    </td>
                                                                    <td>
                                                                        <input value={item.desc}
                                                                               onChange={(e)=>this._handleChangeValue("request_params["+k+"].desc", e)}
                                                                            type="text" className="form-control"/>
                                                                    </td>
                                                                </tr>
                                                            )
                                                        }) : (<></>)
                                                    }
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>


                                        <div className="card border-none">
                                            <div className="card-body">
                                                <h5 className="card-title font-weight-bold">Request Body
                                                    <span
                                                        onClick={() => this._handleAddRequest("body")}
                                                        style={{cursor: "pointer"}}>
                                                        <i className={"fas fa fa-plus-square"}></i>
                                                    </span>
                                                </h5>
                                                <table className="table w-100 table-bordered">
                                                    <thead>
                                                        <tr>
                                                            <th scope="col">Field name</th>
                                                            <th scope="col">Field type</th>
                                                            <th scope="col">Value</th>
                                                            <th scope="col">Description</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                    {
                                                        request.request_body.length>0 ? request.request_body.map((item, k)=>{
                                                            return (
                                                                <tr key={k}>
                                                                    <th scope="row">
                                                                        <input type="text"
                                                                               value={item.key}
                                                                               onChange={(e)=>this._handleChangeValue("request_body["+k+"].key", e)}
                                                                               className="form-control"/>
                                                                    </th>
                                                                    <td>
                                                                        <div className="select-cus">
                                                                            <select
                                                                                value={item.type}
                                                                                onChange={(e)=>this._handleChangeValue("request_body["+k+"].type", e)}
                                                                                className="form-select w-100"
                                                                                    aria-label="Default select example">
                                                                                {opt.field_type.map((ot, ok)=> {
                                                                                        return (
                                                                                            <option key={ok} value={ot}>{ot}</option>
                                                                                        )
                                                                                    }
                                                                                )}
                                                                            </select>
                                                                            <svg width="7" height="6" viewBox="0 0 7 6" fill="none"
                                                                                 xmlns="http://www.w3.org/2000/svg">
                                                                                <path
                                                                                    d="M3.5 6L0.468912 0.749999L6.53109 0.75L3.5 6Z"
                                                                                    fill="#C4C4C4"/>
                                                                            </svg>
                                                                        </div>
                                                                    </td>
                                                                    <td>
                                                                        {
                                                                            item.type == "binary" ? (
                                                                                <input
                                                                                       onChange={(e)=>this._handleChangeValue("request_body["+k+"].value", e)}
                                                                                       type="file" className="form-control"/>
                                                                            ) : (
                                                                                <input value={item.value}
                                                                                       onChange={(e)=>this._handleChangeValue("request_body["+k+"].value", e)}
                                                                                       type="text" className="form-control"/>
                                                                            )
                                                                        }

                                                                    </td>
                                                                    <td>
                                                                        <input value={item.desc}
                                                                               onChange={(e)=>this._handleChangeValue("request_body["+k+"].desc", e)}
                                                                               type="text" className="form-control"/>
                                                                    </td>
                                                                </tr>
                                                            )
                                                        }) : (<></>)
                                                    }


                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                        <div className="d-none card border-none">
                                            <div className="card-body">
                                                <div className="main-card-body-code">
                                                    <h5 className="card-title font-weight-bold"> Request Body Code </h5>
                                                    <nav>
                                                        <div className="nav nav-tabs" id="nav-tab" role="tablist">
                                                            <button className="nav-link active" id="nav-home-tab"
                                                                    data-bs-toggle="tab" data-bs-target="#nav-home"
                                                                    type="button" role="tab"
                                                                    aria-controls="nav-home" aria-selected="true">
                                                                <label>x-www-form-encode</label>
                                                            </button>
                                                            <button className="nav-link" id="nav-profile-tab"
                                                                    data-bs-toggle="tab" data-bs-target="#nav-profile"
                                                                    type="button" role="tab"
                                                                    aria-controls="nav-profile" aria-selected="false">
                                                                <label>Form Data</label>
                                                            </button>
                                                            <button className="nav-link" id="nav-contact-tab"
                                                                    data-bs-toggle="tab" data-bs-target="#nav-contact"
                                                                    type="button" role="tab"
                                                                    aria-controls="nav-contact" aria-selected="false">
                                                                <label>JSON raw</label>
                                                            </button>
                                                        </div>
                                                    </nav>
                                                </div>
                                                <div className="tab-content" id="nav-tabContent">
                                                    <div className="tab-pane fade show active" id="nav-home"
                                                         role="tabpanel" aria-labelledby="nav-home-tab">
                                            <textarea className="form-control" rows="15">

                                            </textarea>
                                                    </div>
                                                    <div className="tab-pane fade" id="nav-profile" role="tabpanel"
                                                         aria-labelledby="nav-profile-tab">
                                            <textarea className="form-control" rows="15">

                                            </textarea>
                                                    </div>
                                                    <div className="tab-pane fade" id="nav-contact" role="tabpanel"
                                                         aria-labelledby="nav-contact-tab">
                                            <textarea className="form-control" rows="15">

                                            </textarea>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-5 pr-0">
                                        <div className="card border-none main-json-code">
                                            <div className="card-body">
                                                <div className="main-json-title">
                                                    <h5 className="card-title font-weight-bold">Response Body</h5>
                                                    <label>Status code:<strong
                                                        className="color-green">{response.status} {response.statusText}</strong></label>
                                                </div>
                                                <div className="main-json">
                                                    {
                                                        _.isString(response.data) ? (
                                                            <div dangerouslySetInnerHTML={{__html: response.data}} >
                                                            </div>
                                                        ) : (
                                                            <JSONTree
                                                                data={response.data}
                                                                hideRoot={true}
                                                                theme={{
                                                                    extend: theme,
                                                                    valueLabel: {
                                                                        textDecoration: 'none',
                                                                    },
                                                                    nestedNodeLabel: ({ style }, keyPath, nodeType, expanded) => ({
                                                                        style: {
                                                                            ...style,
                                                                            fontWeight: expanded ? 'bold' : 'normal',
                                                                        },
                                                                    }),
                                                                }}
                                                            />
                                                        )
                                                    }

                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>


                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default Request;
