import React from 'react';
import axios from "axios";
import { useLocation, Link } from 'react-router-dom'
import * as helper from './../utils/helper';
var x = [];

class Sidebar extends React.Component {
    constructor() {
        super();
        this.state = {
            config: {
                name: "API Test tool & Document"
            },
            menu: {},
            editable: {},
            actived: {},
            dataChange: null
        }

    }

    handleGetRequests = () => {
        axios.get(window._baseUrl+'/apiman/requests').then((res)=>{
            console.log(res)
            if(res.data.length > 0){
                let _folders = [];
                res.data.forEach((item,k)=>{
                    console.log("22", item, k)
                    if(item.parent == 0) {
                        item.childs = this.recursiveRequest(item, res.data)
                        _folders.push(item);
                    }
                })
                console.log('YYY', _folders);
                this.setState({
                    menu: _folders
                })
            }

        })
    }

    recursiveRequest = (item, raw) => {
        let arrs = [];
        raw.forEach((val, key)=>{
            if(val.parent == item.id) {
                val.childs = this.recursiveRequest(val, raw)
                arrs.push(val);
            }
        })
        return arrs;
    }

    _renderMenu = (data) => {
        if(data.length > 0){
            return data.map((item, key)=>{
                return (
                    <li key={key} className={(this.state.actived[item.id]?'active': '') + " "+ (item.type == "FOLDER" ? "folder" : "request")} onClick={()=>this._handleClickMenu(item.id)}>
                        <span>
                            {
                                !this.state.editable[item.id] ? (
                                    item.type == "FOLDER" ? (
                                        <a
                                            onDoubleClick={(e)=>this._handleEditMenu(item.id, e)}
                                        >
                                            {item.name}
                                        </a>
                                    ): (
                                        <Link
                                            onDoubleClick={(e)=>this._handleEditMenu(item.id, e)}
                                            to={"/request/"+item.id}>
                                            <span className={"request_method " + item.request_type}>{item.request_type}</span> {item.name}
                                        </Link>
                                    )
                                ):(
                                    <>
                                        <input
                                            type={"text"}
                                            onKeyUp={(e)=>this._handleChangeMenu(item.id, e)}
                                            defaultValue={item.name}
                                        />
                                        <span onClick={(e)=>this._handleEditMenu(item.id, e)}><i className={"fas fa fa-times"}></i></span>
                                        <span onClick={(e)=>this._handleSaveMenu(item.id, e)}><i className={"fas fa fa-check"}></i></span>
                                    </>

                                )

                            }
                            {(helper.getAuth() && helper.getAuth().role == "editor") ? (
                                <div className="dropdown">
                                    <span className="dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                                        <i className="fas fa-ellipsis-v"></i>
                                    </span>
                                    <ul className="dropdown-menu">
                                        {
                                            item.type=="FOLDER" ? (
                                                <>
                                                <li><a className="dropdown-item"
                                                        onClick={()=>this._handleAddRequest(item.id, "FOLDER")}>Add Folder</a></li>
                                                <li><a className="dropdown-item"
                                                        onClick={()=>this._handleAddRequest(item.id, "REQUEST")}>Add Request</a></li>
                                                <li><a className="dropdown-item"
                                                        onClick={()=>this._handleDeleteRequest(item.id)}>Delete</a></li>
                                                </>
                                            ) : (
                                                <li><a className="dropdown-item"
                                                onClick={()=>this._handleDeleteRequest(item.id)}>Delete</a></li>
                                            )
                                        }

                                    </ul>
                                </div>
                            ):(<></>)}
                        </span>
                        <ul>{this._renderMenu(item.childs)}</ul>
                    </li>
                )
            })
        }
        return (<></>)
    }

    _handleClickMenu = (id, e) => {
        console.log(id)
        this.setState({
            actived: {}
        })
        this.setState({
                actived: {
                    [id]: !this.state.actived[id]
                }
            }
        )
    }

    _handleEditMenu = (id, e) =>{
        let auth = helper.getAuth()
        if(auth.role == "editor"){
            this.setState({
                dataChange: null,
                editable: {
                    [id]: !this.state.editable[id]
                }
            })
        }
        
    }

    _handleChangeMenu = (id, e) => {
        console.log(id, e);
        this.setState({
                dataChange: e.target.value
            })
    }

    _handleSaveMenu = (id, e) => {
        console.log(this.state.dataChange, id, e);
        let data = {
            id: id,
            _method: "PUT",
            name: this.state.dataChange
        };

        axios.post(window._baseUrl+'/apiman/requests', data).then(res=>{
            this.handleGetRequests()
            this.setState({
                    dataChange: null,
                    editable: {
                        [id]: !this.state.editable[id]
                    }
                }
            )
        }, e=>{
            alert("Invalid Name")
        })


    }

    _handleAddRequest = (id, type="FOLDER") =>{
        console.log("add", id, type)
        let data

        if(type == "FOLDER"){
            data = {
                parent: id,
                type: type,
                name: "New Folder"
            }
        }else{
            data = {
                parent: id,
                type: type,
                name: "New Request",
                request_type: "GET",
            }
        }

        axios.post(window._baseUrl+'/apiman/requests', data).then(res=>{
            this.handleGetRequests()
        })
    }

    _handleDeleteRequest = (id) =>{
        console.log("delete", id)
        axios.post(window._baseUrl+'/apiman/requests', {id: id, _method: "DELETE"}).then(res=>{
            this.handleGetRequests()
        })
    }




    componentDidMount() {
        this.handleGetRequests()
    }


    render() {
        return (
            <div className="main-db-left">
                <div className="main-title-logo">
                    <h2><strong className="text-left-login">pri</strong><strong
                        className="text-right-login">api</strong></h2>
                </div>
                <div className="main-left-menu-text">
                    <h5 className="content-title-sidebar">
                        {this.state.config.name}
                        {(helper.getAuth() && helper.getAuth().role == "editor") ? (
                            <div className="dropdown d-inline-block ml-1">
                                <span className="dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                                    <i className={"fa fas fa-plus-square"}></i>
                                </span>
                                <ul className="dropdown-menu">
                                    <li><a className="dropdown-item"
                                            onClick={()=>this._handleAddRequest(0, "FOLDER")}>Add Folder</a></li>
                                    <li><a className="dropdown-item"
                                            onClick={()=>this._handleAddRequest(0, "REQUEST")}>Add Request</a></li>

                                </ul>
                            </div>
                        ) : (
                            <></>
                        )}
                        
                    </h5>
                </div>
                <div className="flex-shrink-0  main-menu-sidebar " style={{width: '280px'}}>
                    <ul className="list-unstyled ps-0">
                        {this._renderMenu(this.state.menu)}
                    </ul>
                </div>
            </div>
        )
    }
}
export default Sidebar;
